import React, { Component } from "react";
import Drawer from "@material-ui/core/Drawer";
import { withStyles } from "@material-ui/core/styles";
import { Typography, InputBase, Grid, IconButton } from "@material-ui/core";

import avatar from "../assets/avatar.svg";
import john from "../assets/john.svg";
import send from "../assets/send-24px.svg";
import Fab from "@material-ui/core/Fab";
import { connect } from "react-redux";
import { FetchBotReply } from "../constants/constants";
import Axios from "axios";
import CloseIcon from "@material-ui/icons/Close";

const styles = (theme) => ({
  messageBox: {
    padding: "10px 10px",
    color: "white",
    display: "inline-block",
    maxWidth: "80%",
  },
  messageText: {
    width: "100%",
    letterSpacing: 0,
    lineHeight: "1rem",
    float: "left",
    fontSize: "0.9em",
    wordWrap: "break-word",
  },
  messageContainer: {
    display: "flex",
    justifyContent: "flex-end",
    padding: "0 2%",
    marginTop: "3px",
  },
  sentText: {
    display: "flex",
    alignItems: "center",
    fontFamily: "Helvetica",
    color: "#828282",
    letterSpacing: "0.3px",
  },
  pl10: {
    paddingLeft: "10px",
  },
  pr10: {
    paddingRight: "10px",
  },
  justifyStart: {
    justifyContent: "flex-start",
  },
  justifyEnd: {
    justifyContent: "flex-end",
  },
  colorWhite: {
    color: "white",
  },
  paper: {
    background: "#1B1F38",
  },
  header: {
    color: "#fff",
    padding: "2rem",
    textTransform: "uppercase",
  },
  input: {
    border: "2px solid #5ca9e3",
    backgroundColor: "#1f2540",
    borderRadius: "50px",
    padding: "5px",
    color: "#fff",
    width: "42vh",
  },
  form: {
    display: "flex",
    bottom: 0,
    marginBottom: "1%",
    position: "fixed",
  },
  btn: {
    borderRadius: "10% !important",
    background: "#5ca9e3 !important",
  },
});

class Professor extends Component {
  constructor(props) {
    super(props);

    this.state = {
      right: true,
      input: "",
      data: props.botReply.content,
    };
  }

  componentWillReceiveProps(newProps) {
    this.setState({
      data: newProps.botReply.content,
    });
  }

  handleChange = (e) => {
    this.setState({
      input: e.target.value,
    });
  };

  fetchBotReply(msg) {
    Axios.post("http://localhost:4000/chat", { message: msg }).then((res) => {
      this.props.dispatch({
        type: FetchBotReply,
        payload: { user: msg, bot: res.data.message },
      });
    });
  }

  handleSendChat = () => {
    this.fetchBotReply(this.state.input);
    this.setState({
      input: "",
    });
  };

  toggleDrawer = (side, open) => () => {
    this.props.close();
  };

  render() {
    const { classes } = this.props;
    const data = this.state.data;
    console.log(data);
    return (
      <div>
        <Drawer
          anchor="right"
          open={this.state.right}
          onClose={this.toggleDrawer("right", false)}
          classes={{ paper: classes.paper }}
        >
          <div
            tabIndex={0}
            role="button"
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
              width: "50vh",
              height: "95vh",
            }}
          >
            <div>
              <Grid container>
                <Grid item xs={9}>
                  <Typography variant="h5" className={classes.header}>
                    Professor
                  </Typography>
                </Grid>
                <Grid item xs={3}>
                  <IconButton
                    onClick={this.toggleDrawer("right", false)}
                    className={classes.header}
                    style={{ marginTop: "5%" }}
                    autoid="professor-close-button"
                  >
                    <CloseIcon></CloseIcon>
                  </IconButton>
                </Grid>
              </Grid>
              <div style={{ overflow: "auto", height: "500px" }}>
                {data.length !== 0 &&
                  data.map((val) => {
                    return (
                      <div>
                        {typeof val.user != "undefined" &&
                          val.user.length !== 0 && (
                            <div
                              className={`${classes.messageContainer} ${classes.justifyEnd}`}
                            >
                              <div
                                className={`${classes.messageBox} ${classes.backgroundPink}`}
                              >
                                <p
                                  className={`${classes.messageText} ${classes.colorWhite}`}
                                  autoid="human"
                                >
                                  {val.user}
                                </p>
                              </div>
                              <p
                                className={`${classes.sentText} ${classes.pr10}`}
                              >
                                <img src={avatar} alt="Prof" />
                              </p>
                            </div>
                          )}
                        {typeof val.bot != "undefined" && val.bot.length !== 0 && (
                          <div
                            className={`${classes.messageContainer} ${classes.justifyStart}`}
                          >
                            <p
                              className={`${classes.sentText} ${classes.pl10}`}
                            >
                              <img src={john} alt="Prof" />
                            </p>
                            <div
                              className={`${classes.messageBox} ${classes.backgroundGrey}`}
                            >
                              <p
                                className={`${classes.messageText} ${classes.colorWhite}`}
                                autoid="ai"
                              >
                                {val.bot}
                              </p>
                            </div>
                          </div>
                        )}
                      </div>
                    );
                  })}
              </div>

              <form className={classes.form}>
                <InputBase
                  type="text"
                  placeholder="Type here..."
                  className={classes.input}
                  value={this.state.input}
                  onChange={this.handleChange}
                  autoid="professor-input-box"
                />
                {this.state.input !== "" ? (
                  <Fab
                    style={{ background: "#5ca9e3", marginTop: "1%" }}
                    size="small"
                    onClick={this.handleSendChat.bind(this)}
                    autoid="professor-send-button"
                  >
                    <img src={send} alt="btn" />
                    {/* Send */}
                  </Fab>
                ) : (
                  <Fab
                    style={{ background: "#5ca9e3", marginTop: "1%" }}
                    size="small"
                    disabled
                    autoid="professor-send-button"
                  >
                    <img src={send} alt="btn" />
                    {/* Send */}
                  </Fab>
                )}
              </form>
            </div>
          </div>
        </Drawer>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  botReply: state.botReply,
});

export default connect(mapStateToProps)(
  withStyles(styles, { withTheme: true })(Professor)
);
