import React, { Component } from "react";
import {
  Card,
  CardContent,
  Paper,
  Table,
  TableHead,
  TableBody,
  TableCell,
  TableRow,
  InputBase,
  IconButton,
  TextField,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  CircularProgress,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import SearchIcon from "@material-ui/icons/Search";
import { fade } from "@material-ui/core/styles/colorManipulator";
import { formatter } from "../utils/formatter";
import { FetchSearch, url } from "../constants/constants";
import Axios from "axios";
import ClearIcon from "@material-ui/icons/Clear";
import attach_money from "../assets/attach_money.svg";

const styles = (theme) => ({
  card: {
    backgroundColor: "#252C48",
    height: "30vh",
    overflow: "auto",
  },
  root: {
    width: "100%",
    marginTop: theme.spacing.unit * 1,
    backgroundColor: "#252C48",
    overflow: "auto",
    height: "17vh",
  },
  rootSearch: {
    width: "100%",
    marginTop: theme.spacing.unit * 1,
    backgroundColor: "#252C48",
    marginBottom: theme.spacing.unit * 10,
  },
  table: {
    minWidth: 100,
    backgroundColor: "#252C48",
  },
  tableHeader: {
    padding: "5px 5px 5px 15px",
    color: "#fff",
  },
  textColor: {
    fontSize: "62%",
    color: "#fff !important",
  },
  input: {
    width: "100%",
  },
  link: {
    color: "#fff",
    textDecoration: "none",
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius * 5,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    width: "100%",
  },
  searchIcon: {
    width: theme.spacing.unit * 5,
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  attach: {
    marginTop: -theme.spacing.unit * 5,
  },
  attachCross: {
    marginTop: -theme.spacing.unit * 4.7,
  },

  inputRoot: {
    color: "inherit",
    width: "100%",
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 5,
    transition: theme.transitions.create("width"),
    width: "100%",
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
  select: {
    color: "white",
    "&:before": {
      borderColor: "white",
    },
    "&:after": {
      borderColor: "white",
    },
  },
  icon: {
    fill: "white",
  },
  menuRoot: {
    backgroundColor: "252C48",
  },
  loader: {
    display: "flex",
    justifyColumn: "center",
    alignItems: "center",
    padding: "3rem",
    marginLeft: "10rem",
  },
  colorPrimary: {
    color: "#fff",
  },
});

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchVal: "",
      searched: false,
      searchedData: [],
      openSearch: false,
      openAmount: "",
      op: "",
      openSelect: false,
    };
  }

  handleSearchChange = (e) => {
    this.setState({ searchVal: e.target.value });
  };

  keyPressEnter = (e) => {
    if (e.keyCode === 13 && this.state.searchVal !== "") {
      for (var i = 0; i < this.props.search.length; i++) {
        if (
          this.state.searchVal === this.props.search[i].customer_name ||
          this.state.searchVal === this.props.search[i].customer_number
        ) {
          this.setState({
            searchedData: [this.props.search[i]],
            searched: true,
          });
          return;
        }
      }
      this.setState({
        searchedData: [],
        searched: true,
      });
    } else {
      this.setState({
        searchedData: [],
        searched: false,
      });
    }
  };
  fetchSearch = () => {
    Axios.get(url + "/SimpleSearchAll")
      .then((res) => {
        this.props.dispatch({
          type: FetchSearch,
          payload: res.data,
        });
      })
      .catch((e) => {
        console.log(e);
      });
  };

  componentWillMount() {
    this.fetchSearch();
  }

  handleOpenSearch() {
    this.setState({
      openSearch: !this.state.openSearch,
    });
  }
  handleCloseSearch() {
    this.setState({
      openSearch: false,
      op: "",
      openAmount: "",
    });
  }

  handleChangeOpenAmount(e) {
    this.setState({
      openAmount: e.target.value,
    });
  }

  handleCompareOperator(e) {
    this.setState({
      op: e.target.value,
    });
  }

  handleOpenSelect(e) {
    this.setState({
      openSelect: true,
    });
  }
  handleCloseSelect(e) {
    this.setState({
      openSelect: false,
    });
  }

  handleSearchSubmit() {
    var data = [];
    for (var i = 0; i < this.props.search.length; i++) {
      console.log(
        formatter(this.props.search[i].actual_open_amount),
        formatter(this.state.openAmount)
      );
      if (
        this.state.op === "=" &&
        formatter(this.props.search[i].actual_open_amount) ===
          formatter(parseFloat(this.state.openAmount))
      )
        data.push(this.props.search[i]);
      if (
        this.state.op === "<=" &&
        this.props.search[i].actual_open_amount <=
          parseFloat(this.state.openAmount)
      )
        data.push(this.props.search[i]);
      if (
        this.state.op === ">=" &&
        this.props.search[i].actual_open_amount >=
          parseFloat(this.state.openAmount)
      )
        data.push(this.props.search[i]);
      if (
        this.state.op === ">" &&
        this.props.search[i].actual_open_amount >
          parseFloat(this.state.openAmount)
      )
        data.push(this.props.search[i]);
      if (
        this.state.op === "<" &&
        this.props.search[i].actual_open_amount <
          parseFloat(this.state.openAmount)
      )
        data.push(this.props.search[i]);
      if (
        this.state.op === "!=" &&
        formatter(this.props.search[i].actual_open_amount) !==
          formatter(parseFloat(this.state.openAmount))
      )
        data.push(this.props.search[i]);
    }

    this.setState({
      searchVal:
        "Customers with " +
        this.state.op +
        " $" +
        this.state.openAmount +
        " OpenAmount",
      op: "",
      searchedData: data,
      openAmount: "",
      openSearch: false,
      searched: true,
    });
  }

  handleClear() {
    this.setState({
      searchData: [],
      searched: false,
      searchVal: "",
    });
  }

  render() {
    const { classes } = this.props;
    const { searchVal } = this.state;
    const searchData = this.state.searched
      ? this.state.searchedData
      : this.props.search;
    if (this.props.search.length === 0) {
      return (
        <div className={classes.loader}>
          <CircularProgress className={classes.colorPrimary} />
        </div>
      );
    }
    return (
      <Card className={classes.card}>
        <CardContent>
          {/*<input
            type="text"
            placeholder="Search by customer name or customer number"
            className={classes.input}
            value={searchVal}
            onChange={this.handleSearchChange}
            onKeyDown={this.keyPressEnter}
          />*/}
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon style={{ color: "white" }} autoid="search-icon" />
            </div>
            <InputBase
              placeholder="Search by customer name or customer number"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              value={searchVal}
              onChange={this.handleSearchChange}
              onKeyDown={this.keyPressEnter.bind(this)}
              style={{
                fontSize: "12px",
                width: "100%",
                color: "white",
              }}
              autoid="search-text-field"
            />
          </div>
          <IconButton
            onClick={this.handleOpenSearch.bind(this)}
            style={{ float: "right" }}
            className={classes.attach}
            autoid="advance-search-button"
          >
            <img
              src={attach_money}
              height="27vh"
              style={{ color: "#bdbdbd" }}
              alt={"S"}
            ></img>
          </IconButton>
          {this.state.searchVal !== "" ? (
            <IconButton
              onClick={this.handleClear.bind(this)}
              style={{ float: "right", color: "white" }}
              className={classes.attachCross}
              autoid="search-close-icon"
            >
              <ClearIcon></ClearIcon>
            </IconButton>
          ) : null}
          <br />
          <br />
          {this.state.openSearch ? (
            <Paper
              className={classes.rootSearch}
              elevation={0}
              style={{ marginTop: "-10%" }}
            >
              <FormControl
                className={classes.formControl}
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <InputLabel style={{ color: "white" }}>Operation</InputLabel>
                <Select
                  open={this.state.openSelect}
                  onClose={this.handleCloseSelect.bind(this)}
                  onOpen={this.handleOpenSelect.bind(this)}
                  value={this.state.op}
                  onChange={this.handleCompareOperator.bind(this)}
                  className={classes.select}
                  inputProps={{
                    classes: {
                      icon: classes.icon,
                    },
                  }}
                  fullWidth
                  autoid="advance-search-drop-down"
                >
                  <MenuItem value={">="}>Greater than or equal to</MenuItem>
                  <MenuItem value={"<="}>Lesser than or equal to</MenuItem>
                  <MenuItem value={"="}>equal to</MenuItem>
                  <MenuItem value={">"}>Greater than</MenuItem>
                  <MenuItem value={"<"}>Lesser than </MenuItem>
                  <MenuItem value={"!="}>Not equal to</MenuItem>
                </Select>
              </FormControl>
              <TextField
                id="filled-full-width"
                label="Open Amount"
                placeholder="Don't leave this empty"
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                  style: { color: "white" },
                }}
                inputProps={{
                  style: { color: "white", height: "2vh", fontSize: "smaller" },
                }}
                value={this.state.openAmount}
                onChange={this.handleChangeOpenAmount.bind(this)}
                variant="filled"
                autoid="advance-search-open-amount"
              />
              {this.state.openAmount !== "" && this.state.op !== "" ? (
                <Button
                  onClick={this.handleSearchSubmit.bind(this)}
                  style={{
                    color: "white",
                    backgroundColor: "#0091ea",
                    float: "right",
                  }}
                  variant="contained"
                  size="small"
                  autoid="modify-save-button"
                >
                  Save
                </Button>
              ) : (
                <Button
                  style={{ color: "#9e9e9e", float: "right" }}
                  variant="contained"
                  size="small"
                  disabled
                  autoid="modify-save-button"
                >
                  Save
                </Button>
              )}
              <Button
                onClick={this.handleCloseSearch.bind(this)}
                style={{
                  color: "#90a4ae",
                  borderColor: "#90a4ae",
                  float: "right",
                  marginRight: "1%",
                }}
                variant="outlined"
                size="small"
                autoid="advance-search-cancel"
              >
                Cancel
              </Button>
            </Paper>
          ) : null}
          <Paper
            className={classes.root}
            elevation={0}
            style={{ marginTop: "-10%" }}
          >
            <Table className={classes.table} autoid="advance-search-table">
              <TableHead>
                <TableRow>
                  <TableCell className={classes.textColor}>
                    Customer Name
                  </TableCell>
                  <TableCell className={classes.textColor}>
                    Customer Number
                  </TableCell>
                  <TableCell className={classes.textColor}>
                    Open Amount
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {searchData.map((d, i) => {
                  return (
                    <TableRow key={i}>
                      <TableCell className={classes.textColor}>
                        <Link
                          className={classes.link}
                          to={{
                            pathname: `/customer-dashboard/`,
                            state: {
                              id: d.customer_number,
                              name: d.customer_name,
                            },
                          }}
                        >
                          {d.customer_name}
                        </Link>
                      </TableCell>
                      <TableCell className={classes.textColor}>
                        {d.customer_number}
                      </TableCell>
                      <TableCell className={classes.textColor}>
                        {"$" + formatter(d.actual_open_amount)}
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </Paper>
        </CardContent>
      </Card>
    );
  }
}

const mapStateToProps = (state) => ({
  search: state.search,
});

export default connect(mapStateToProps)(
  withStyles(styles, { withTheme: false })(Search)
);
